import React, { Component } from 'react';
import styled from 'styled-components';
import FormHeader from '../components/FormHeader';
import CardReceivingType from '../components/CardReceivingType';
import CardType from '../components/CardType';
import AuthForm from '../components/AuthForm';

const Wrapper = styled.div`
  width: 940px;
  margin: 0 auto;
  padding: 30px;
  border: 1px solid #cbcdcc;
  border-bottom: none;
`;

const FormBody = styled.div`
  width: 100%;
  height: 500px;
  padding-top: 20px;
`;

class CardForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentStep: 1,
      receiveType: 'choosingType',
      priceCard: 700,
      stepPassed: false
    };
  }

  nextStep = () => {
    this.setState(prevState => ({
      currentStep: prevState.currentStep + 1
    }));
  };

  stepNotPassed = () => {
    this.setState({ stepPassed: false });
  };

  changeStep = step => {
    this.setState({ currentStep: step });
  };

  setReceiveType = type => {
    this.setState({ receiveType: type, stepPassed: true });
  };

  setPriceCard = price => {
    this.setState({ priceCard: price });
  };

  render() {
    const { receiveType, currentStep, priceCard, stepPassed } = this.state;
    const mapSteps = {
      choosingType: {
        1: (
          <CardReceivingType
            setReceiveType={this.setReceiveType}
            receiveType={receiveType}
          />
        )
      },
      individual: {
        2: <CardType priceCard={priceCard} setPriceCard={this.setPriceCard} />,
        3: <AuthForm stepNotPassed={this.stepNotPassed} />,
        4: <div>individual 4 step</div>
      },
      collective: {
        2: <AuthForm stepNotPassed={this.stepNotPassed} />,
        3: <div>collective 3 step</div>,
        4: <div>collective 4 step</div>
      }
    };
    return (
      <Wrapper>
        <FormHeader
          currentStep={currentStep}
          nextStep={this.nextStep}
          changeStep={this.changeStep}
          stepPassed={stepPassed}
        />
        <FormBody>
          {mapSteps[receiveType][currentStep] || mapSteps['choosingType'][1]}
        </FormBody>
      </Wrapper>
    );
  }
}

export default CardForm;
