import styled from 'styled-components';
import React from 'react';
import listicon from '../../img/list-icon.png';

export const H1 = styled.h1`
  font-size: 1.625em;
  font-weight: 700;
  color: #48453c;
  font-family: 'Fira Sans', sans-serif;
  text-align: center;
`;

export const H2 = styled.h2`
  font-size: 1.625em;
  font-weight: 700;
  color: #f31c4c;
  font-family: 'Fira Sans', sans-serif;
  text-align: center;
  margin-top: 90px;
`;

export const H3 = styled.h3`
  font-size: 1.625em;
  color: #c8c8c8;
  font-family: 'Fira Sans', sans-serif;
  text-align: center;
  text-transform: upercase;
`;

export const CardsBlock = styled.div`
  display: flex;
  justify-content: space-between;
  position: relative;
  width: 100%;
  height: 400px;
  padding-top: 40px;
  & label {
    font-family: 'Fira Sans', sans-serif;
    font-size: 14px;
    color: #939393;
  }

  & input {
    margin-left: 20px;
  }
`;

export const ExchangeCard = styled.div`
  width: 33%;
  cursor: pointer;
`;
export const BasicCard = styled(ExchangeCard)``;
export const ProtectionCard = styled(ExchangeCard)``;

export const ProtectionCardShow = styled.div`
  position: absolute;
  right: 0;
  top: 0;
  width: 390px;
  background: #fff;
  transition: top 1s ease-out 0.5s;
  & h1 {
    font-size: 1.625em;
    font-weight: 700;
    color: #f31c4c;
    font-family: 'Fira Sans', sans-serif;
    text-align: center;
    margin: 0 0 15px 0;
  }
  & ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  & li {
    padding: 5px 0 5px 25px;
    position: relative;
    font-size: 0.9em;
    color: #47433d;
    font-family: 'Fira Sans', sans-serif;
    &:before {
      position: absolute;
      left: 0;
      top: 7px;
      content: '';
      display: inline-block;
      width: 15px;
      height: 15px;
      background: url(${listicon}) no-repeat;
      background-position: center;
      background-size: contain;
    }
  }
`;
export const BasicCardShow = styled(ProtectionCardShow)`
  left: 28%;
`;
export const ExchangeCardShow = styled(ProtectionCardShow)`
  left: 0;
`;

export const SvgCard = {
  mini: (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
      width="299px"
      height="200px"
    >
      <image
        x="0px"
        y="0px"
        width="299px"
        height="200px"
        xlinkHref="data:img/png;base64,"
      />
    </svg>
  ),
  big: (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
      width="400px"
      height="300px"
    >
      <image
        x="0px"
        y="0px"
        width="400px"
        height="300px"
        xlinkHref="data:img/png;base64,"
      />
    </svg>
  )
};
