import styled from 'styled-components';

export const H1 = styled.h1`
  font-size: 1.625em;
  font-weight: 700;
  color: #48453c;
  font-family: 'Fira Sans', sans-serif;
  text-align: center;
`;

export const H2 = styled.h2`
  font-size: 1.4375em;
  font-weight: 700;
  text-transform: uppercase;
  color: ${props => (props.active ? '#f31c4c' : '#c9c9c9')};
`;

export const ReceivingTypes = styled.div`
  display: flex;
  justify-content: flex-end;
  font-family: 'Fira Sans', sans-serif;

  & span {
    font-size: 12px;
    font-weight: 400;
    color: #46433d;
  }
  & div {
    width: 350px;
    cursor: pointer;
  }
`;

export const IndividualType = styled.div``;

export const CollectivelyType = styled.div``;
