import React, { Component } from 'react';
import { Header, BreadCrumbs, Steps, Step, Button } from './styles.js';

class FormHeader extends Component {
  changeStep = step => {
    const currentStep = this.props.currentStep;
    const changeStep = this.props.changeStep;
    if (currentStep > step) {
      changeStep(step);
    }
  };

  showSteps = () => {
    const currentStep = this.props.currentStep;
    const steps = [1, 2, 3, 4];
    return steps.map(step => {
      return (
        <Step key={step} active={step <= currentStep}>
          <a href="#" onClick={() => this.changeStep(step)}>
            <span>{step}</span>
          </a>
        </Step>
      );
    });
  };

  render() {
    const { nextStep, stepPassed } = this.props;
    return (
      <Header>
        <BreadCrumbs>
          <a href="#">Главная</a>
          <a href="#">Заказать карту</a>
        </BreadCrumbs>
        <Steps>{this.showSteps()}</Steps>
        <Button onClick={stepPassed ? nextStep : null}>Далее</Button>
      </Header>
    );
  }
}

export default FormHeader;
