import React, { Component } from 'react';
import validator from 'validator';
import moment from 'moment';
import {
  H1,
  Forms,
  NameInput,
  DatePhone,
  MaskedInput,
  CheckboxContainer,
  HiddenCheckbox,
  StyledCheckbox,
  Icon,
  CheckBoxBlock,
  SpecialMarks,
  EmailInput,
  ErrorText
} from './styles.js';

const Checkbox = ({ className, checked, ...props }) => (
  <CheckboxContainer className={className}>
    <HiddenCheckbox checked={checked} {...props} />
    <StyledCheckbox checked={checked}>
      <Icon viewBox="0 0 24 24">
        <polyline points="20 6 9 17 4 12" />
      </Icon>
    </StyledCheckbox>
  </CheckboxContainer>
);

class AuthForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      emailChecked: false,
      specMarkChecked: false,
      error: null
    };
  }

  componentDidMount() {
    this.props.stepNotPassed();
  }
  handleCheckboxChange = e => {
    const type = e.target.name;
    const checked = e.target.checked;
    this.setState({ [type]: checked });
  };

  getInputValue = e => {
    const data = { type: e.target.name, value: e.target.value };
    if (this.validateValue(data)) {
      this.setState({ [data.type]: data.value });
    }
  };

  validateValue = data => {
    switch (data.type) {
      case 'name':
        if (data.value.length < 2) {
          this.setState({ error: 'Короткое Имя' });
          return false;
        } else {
          this.setState({ error: null });
          return true;
        }
      case 'date':
        if (!moment(data.value, 'DD/MM/YYYY', true).isValid()) {
          this.setState({ error: 'Введите корректную дату' });
          return false;
        } else {
          this.setState({ error: null });
          return true;
        }
      case 'phone':
        if (data.value.length < 2) {
          this.setState({ error: 'Введите корретный контактный номер' });
          return false;
        } else {
          this.setState({ error: null });
          return true;
        }
      case 'email':
        if (!validator.isEmail(data.value)) {
          this.setState({ error: 'Введите корректную почту' });
          return false;
        } else {
          this.setState({ error: null });
          return true;
        }

      default:
        return false;
    }
  };

  render() {
    const { emailChecked, specMarkChecked, error } = this.state;
    return (
      <>
        <H1>Авторизация</H1>
        <Forms>
          {error === null ? '' : <ErrorText>{error}</ErrorText>}
          <NameInput
            placeholder="Фамилия Имя Отчество"
            onChange={this.getInputValue}
            name="name"
          />
          <DatePhone>
            <MaskedInput
              showMask
              maskString="ДД/ММ/ГГГГ"
              mask="00/00/0000"
              placeholder="Дата рождения"
              name="date"
              onChange={this.getInputValue}
            />
            <MaskedInput
              showMask
              maskString="7-000-000-00-00"
              mask="0-000-000-00-00"
              placeholder="Контактный телефон"
              name="phone"
              onChange={this.getInputValue}
            />
          </DatePhone>
          <CheckBoxBlock>
            <label>
              <Checkbox
                name="emailChecked"
                checked={emailChecked}
                onChange={this.handleCheckboxChange}
              />
              <span>E-mail</span>
              <EmailInput name="email" onChange={this.getInputValue} />
            </label>
            <SpecialMarks>
              <span>*</span>Получение уведомлений о новостях на сайте
            </SpecialMarks>
          </CheckBoxBlock>
          <CheckBoxBlock>
            <label>
              <Checkbox
                name="specMarkChecked"
                checked={specMarkChecked}
                onChange={this.handleCheckboxChange}
              />
              <span>
                Согласие <br />
                на обработку персональных данных
              </span>
            </label>
            <SpecialMarks>
              <span>*Внимание!</span> Для перехода на следующий этап необходимо
              поставить отметки в окошках
            </SpecialMarks>
          </CheckBoxBlock>
        </Forms>
      </>
    );
  }
}

export default AuthForm;
