import React, { Component } from 'react';
import {
  H1,
  H2,
  ReceivingTypes,
  IndividualType,
  CollectivelyType
} from './styles.js';

class CardReceivingType extends Component {
  render() {
    const { setReceiveType, receiveType } = this.props;
    return (
      <>
        <H1>Выбор способа получения карты</H1>
        <ReceivingTypes>
          <IndividualType
            onClick={() => {
              setReceiveType('individual');
            }}
          >
            {receiveType === 'individual' ? (
              <div>
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  xmlnsXlink="http://www.w3.org/1999/xlink"
                  width="250px"
                  height="300px"
                >
                  <image
                    x="0px"
                    y="0px"
                    width="250px"
                    height="300px"
                    xlinkHref="data:img/png;base64,"
                  />
                </svg>
                <H2 active>Индивидуально</H2>
              </div>
            ) : (
              <div>
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  xmlnsXlink="http://www.w3.org/1999/xlink"
                  width="250"
                  height="300"
                >
                  <image
                    width="250"
                    height="300"
                    xlinkHref="data:img/png;base64,"
                  />
                </svg>
                <H2>Индивидуально</H2>
              </div>
            )}

            <span>если Вы хотите получить одну карту «Проф Плюс»</span>
          </IndividualType>
          <CollectivelyType
            onClick={() => {
              setReceiveType('collective');
            }}
          >
            {receiveType === 'collective' ? (
              <div>
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  xmlnsXlink="http://www.w3.org/1999/xlink"
                  width="250px"
                  height="300px"
                >
                  <image
                    x="0px"
                    y="0px"
                    width="250px"
                    height="300px"
                    xlinkHref="data:img/png;base64,"
                  />
                </svg>
                <H2 active>Коллективно</H2>
              </div>
            ) : (
              <div>
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  xmlnsXlink="http://www.w3.org/1999/xlink"
                  width="250px"
                  height="300px"
                >
                  <image
                    x="0px"
                    y="0px"
                    width="250px"
                    height="300px"
                    xlinkHref="data:img/png;base64,"
                  />
                </svg>
                <H2>Коллективно</H2>
              </div>
            )}

            <span>
              если Вы являетесь организацией и хотите заказать карту «Проф Плюс»
              для Вашего коллектива от 10 человек
            </span>
          </CollectivelyType>
        </ReceivingTypes>
      </>
    );
  }
}

export default CardReceivingType;
